*{
  padding: 0%;
  margin: 0%;
}
body{
  background-color: rgb(42, 42, 42);
}
.todo-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 5rem;
  gap: 1rem;
}
.todo-head{
  color: aliceblue;
  font-size: 2.5rem;
}
.todo-inputs{
  border-radius: 15px 0px 0px 15px;
  padding: 0.7rem;
  border: none;
  font-size: 1.2rem;
  width: 38vh;
  outline: none;
}
.todo-add{
  border-radius: 0px 15px 15px 0px;
  padding: 0.7rem 1rem 0.7rem 1rem;
  border: none;
  background-color: rgb(34, 126, 255);
  color: aliceblue;
  font-size: 1.2rem;
  cursor: pointer;
}
.todo-add:hover{
  background-color: rgb(208, 208, 0);
  color: black;
}
.list{
  color: black;
  list-style: none;
  text-decoration: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 25px;
  box-sizing: 20vh;
  max-width: auto;
  padding: 0.7rem 3.7rem 0.7rem 1rem;
  border: 2px solid wheat;
  background-color: whitesmoke;
  margin: 1rem;
  font-size: 1.2rem;
}
.list-text{
  margin: 0rem 1rem 0rem 1rem;
  font-size: 1.5rem;
  box-sizing: 10vh;
}
.icons{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  
}
.tick{
  font-size: 2rem;
  color: aliceblue;
  background-color: green;
  border-radius: 50%;
  cursor: pointer;
}
.cancel{
  font-size: 1.2rem;
  padding: 0.5rem;
  color: aliceblue;
  background-color: rgb(190, 0, 0);
  border-radius: 50%;
  cursor: pointer;
}
.date-time{
  color: aliceblue;
  font-size: 1.5rem;
}
.clear-all{
  padding: 1rem;
  border-radius: 10px;
  font-size: 1rem;
  border: none;
  font-weight: 600;
  background-color: rgb(162, 24, 0);
  color: aliceblue;
  cursor: pointer;
}

@media screen and (max-width: 480px){
  .todo-head{
    font-size: 2rem;
  }
  .date-time{
    font-size: 1rem;
  }
  .todo-inputs{
    width: 25vh;
    font-size: 1rem;
  }
  .todo-add{
    font-size: 0.7rem;
  }
  form{
    display: flex;
  }
}